// 引入攻略列表的数据库接口
import {datalist} from '../../../common/cloudfun.js'
export default{
	name:"tab",
	props:{
		tab:Array
	},
	data(){
		return{
			num: 0
		}
	},
	methods:{
		tabs(index,nav){
			this.num = index
			//加载状态修改
			let loadingstatus = true
			this.$store.commit('loadmuta', loadingstatus)
			
			datalist(nav)
			.then((res) => {
				//console.log(res)
				// vuex传值 
				let listdata = res.data
				this.$store.commit('listmuta', listdata)
				//加载状态修改
				let loadingstatus = false
				this.$store.commit('loadmuta', loadingstatus)
			})
			.catch((err) => {
				console.log(err)
			})
		}
	}
}
